<template>
  <div style="width: 80%; margin: auto;">
      <div style="margin-top: 10px;">
          <el-row type="flex" class="row-bg" justify="center">
            <el-col :span="10"><el-input v-model="input" placeholder="请输入想要搜索的内容" @change="findSome"/></el-col>
            <el-col :span="2"><el-button type="primary" @click="findSome">搜索</el-button></el-col>
          </el-row>
          <!-- 标签 -->
          <div style="margin-top: 10px;">
              <el-switch v-model="value1" inactive-text="css" @change="findSome"></el-switch>
              <el-switch style="margin-left: 10px" v-model="value2" inactive-text="js" @change="findSome"></el-switch>
          </div>
      </div>
      <div>
          <el-card class="box-card" v-for="(item, index) in showData" :key="index">
            <div slot="header" class="clearfix">
                <span>{{item.name}}</span>
            </div>
            <div class="text item">
                <p v-for="(ele, index) in item.text" :key="index">{{ele}}</p>
            </div>
           </el-card>
           <el-pagination
            background
            :hide-on-single-page="value"
            layout="prev, pager, next"
            :total="dataList.length"
            :current-page.sync="currentPage"
            @current-change="currentChange">
            </el-pagination>
      </div>
  </div>
</template>

<script>
import listData from '@/assets/json/page3List.json'
export default {
    data() {
        return {
            currentPage: 1,
            value: false,
            value1: true,
            value2: true,
            input: '',
            dataList: listData.page3List,
            showData: []
        }
    },
    created() {
        if (this.dataList.length <= 10) {
            this.value = true
        }
        this.currentChange()
    },
    methods: {
        //切换页 每页只显示10个
        currentChange() {
            let pa = this.currentPage
            this.showData = this.dataList.slice(10*(pa-1),9+10*(pa-1))
        },
        //搜索
        findSome() {
            // 标签的过滤
            let tagList = []
            listData.page3List.forEach(item => {
                if (this.value1 && item.tag.indexOf("css") != -1) {
                    tagList.push(item)
                }
                if (this.value2 && item.tag.indexOf("js") != -1) {
                    tagList.push(item)
                }
            })
            // 搜索的过滤
            if (this.input == '') {
                this.dataList = tagList
            } else {
                let findList = []
                tagList.forEach(ele => {
                    ele.text.forEach(item => {
                        if (ele.name.indexOf(this.input) != -1 || item.indexOf(this.input) != -1) {
                            findList.push(ele)
                        }
                    })
                    
                });
                this.dataList = findList
            }
            if (this.dataList.length <= 10) {
                    this.value = true
            } else {
                this.value = false
            }
            this.currentPage = 1
            this.currentChange()
        }
    }
}
</script>

<style lang="less" scoped>
.text {
    font-size: 14px;
    text-align: left;
  }

  .item {
    margin: 10px 0px 18px 10px;
  }
</style>